// 
$black: #333;
$font-family-base: 'Inter',
sans-serif;

// $spacer
@import "./scss/foundation/spacer";

/* $theme-colors */
$primary: #7e0f83;
$secondary: #364799;
$success: #1F501D;
$danger: #BE1A1C;
$warning: #FDB448;
$info: #cea6cc;
$light: #f9f9f9;
$dark: #212529;

/*$theme-colors */
$theme-colors: (
  "primary": $primary,
  "secondary": $secondary,
  "success": $success,
  "info": $info,
  "warning": $warning,
  "danger": $danger,
  "light": $light,
  "dark": $dark
  );

/*$custom-colors */
$custom-colors: (
  "blue-light": #E1F3FF,
  "blue-main": #1F5AD9,
  "blue-dark": #184880,
  "gray-light": #f9f9f9,
  "accent": #FDB448,
);

/*map-merge   $theme-colors  $custom-colors */
$theme-colors: map-merge($theme-colors, $custom-colors);

/* Bootstrap  Sass  */
@import "~bootstrap/scss/bootstrap.scss";

// ==============================================
// scss
// ==============================================
// foundation
@import "./scss/foundation/variable";
@import "./scss/foundation/mixin";
// layout
@import "./scss/layout/l-header";
// component
@import "./scss/object/component/wrapper";
@import "./scss/object/component/heading";
// utility
@import "./scss/object/utility/block";
@import "./scss/object/utility/img";
// project
@import "./scss/object/project/p-header";
@import "./scss/object/project/fv";
@import "./scss/object/project/cosme";
@import "./scss/object/project/dev";